#Services h6,
#Services h2 {
    text-align: center;
}

#Services {
    background-image: url("../../assets/Home/bg.jpg");
}

.services-container {
    display: grid;
    justify-items: center;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin: 0 15% 0 15%;
}

.service-head {
    margin-top: 3rem;
    text-align: center;
}

.service {
    background-image: linear-gradient(rgb(130, 130, 130), #1e1e4a);
    border-radius: 3rem 3rem 2rem 2rem;
    margin: 1rem;
    color: white;
    width: 250px;
}

.footer-container {
    height: 5%;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
}

.footer-parent {
    width: 100%;
    height: 100%;
    max-width: 2000px;
}

.footer-parent img {
    max-width: 100%;
    height: 150px;
}

@media only screen and (max-width:1110px) {
    .footer-parent {
        display: flex;
        align-items: flex-end;
    }
}

@media screen and (min-width:1110px) {
    .services-container {
        grid-template-columns: repeat(4, 1fr);
        gap: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:1110px) {
    .services-container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
        margin: 0 15% 0 15%;
    }
    .service {
        width: 200px;
    }
}

@media screen and (max-width:910px) {
    .services-container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:810px) {
    .services-container {
        grid-template-columns: 1fr;
        gap: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:375px) {
    .services-container {
        grid-template-columns: 1fr;
        gap: 2rem;
        margin: 0 15% 0 15%;
    }
}