#Portfolio h6,
#Portfolio h2 {
    text-align: center;
}

.portfolio-container {
    display: grid;
    justify-items: center;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin: 0 15% 0 15%;
}

.portfolio-item {
    background: #24263b;
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: 1s ease-out;
    color: white;
    width: 95%;
    height: 90%;
}

.portfolio-item:hover {
    border-color: blue;
    background: transparent;
    color: black;
}

.portfolio-item-image {
    border-radius: .8rem;
    overflow: hidden;
    aspect-ratio: 4/3;
}

.portfolio-item-image>img {
    aspect-ratio: 4/3;
    width: 100%;
}

.portfolio-item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio-item-cta {
    margin: 10% 20% 1rem 0;
}

.portfolio-item-cta a {
    margin-right: 10px;
    text-decoration: none;
}

.hub {
    color: var(--dark-orange);
}

.git {
    padding: 5px;
    border-radius: 20px;
}

@media screen and (min-width:1110px) {
    .portfolio-container {
        grid-template-columns: repeat(3, 1fr);
        gap: 2.5rem;
    }
}

@media screen and (max-width:1110px) {
    .portfolio-container {
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
}

@media screen and (max-width:910px) {
    .portfolio-container {
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
}

@media screen and (max-width:810px) {
    .portfolio-container {
        grid-template-columns: 1fr;
        gap: 2.5rem;
    }
}

@media screen and (max-width:375px) {
    .portfolio-container {
        grid-template-columns: 1fr;
        gap: 2.5rem;
    }
    .portfolio-item-cta>a {
        font-size: 10px;
    }
}