footer {
    margin-top: 5rem;
    background: #24263b;
    padding: 3rem 0;
    text-align: center;
    font-size: .9rem;
}

footer a {
    color: white;
    text-decoration: none;
}

.footer-logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: .5rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    padding: 0;
    margin: 0 auto 2rem;
    list-style: none;
}

.footer-socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}

.footer-socials a {
    background: #2c2c6c;
    color: aliceblue;
    padding: 0.8rem;
    border-radius: .7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer-socials a:hover {
    background: transparent;
    color: var(--dark-orange);
    border-color: blue;
}

.footer-copyright {
    margin-bottom: 4rem;
    color: white;
}

@media screen and (max-width:810px) {
    .portfolio-container {
        grid-template-columns: 1fr;
        gap: 2.5rem;
    }
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }
}

@media screen and (max-width:375px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }
}