#Resume h6,
#Resume h2 {
    text-align: center;
}

.resume-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    justify-items: center;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin: 0 15% 0 15%;
}

.resume-container>div {
    background: #2c2c6c;
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    color: white;
    transition: 1s ease-out;
}

.resume-container>div:hover {
    background: transparent;
    border-color: #2c2c6c;
    cursor: default;
    color: black;
}

.resume-container>div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--dark-orange);
}

.experience-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.ex-icon {
    margin-top: 1.5rem;
}

.experience-details>h4 {
    margin: 5px;
}

.experience-details {
    display: flex;
    gap: 1rem;
}

.ex-icons {
    display: flex;
    flex-direction: column;
}

@media screen and (min-width:1110px) {
    .resume-container {
        grid-template-columns: 1fr 1fr;
    }
    .resume-container>div {
        width: 70%;
        padding: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:1110px) {
    .resume-container {
        grid-template-columns: 1fr 1fr;
    }
    .resume-container>div {
        width: 80%;
        padding: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:910px) {
    .resume-container {
        grid-template-columns: 1fr 1fr;
    }
    .resume-container>div {
        width: 80%;
        padding: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:810px) {
    .resume-container {
        grid-template-columns: 1fr;
    }
    .resume-container>div {
        width: 90%;
        padding: 2rem;
        margin: 0 15% 0 15%;
    }
}

@media screen and (max-width:375px) {
    .resume-container {
        grid-template-columns: 1fr;
    }
    .resume-container>div {
        width: 90%;
        padding: 1rem;
        margin: 0 15% 0 15%;
    }
}