    .about-container {
        display: flex;
    }
    
    #AboutMe {
        text-align: center;
    }
    
    .about-cards {
        gap: 1.5rem;
        margin: 0% 30% 0% 30%;
    }
    
    .about-card {
        background: #2c2c6c;
        border: 1px solid transparent;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;
        color: white;
        transition: 1s ease-out;
        margin: 5% 0 5% 0;
    }
    
    .about-card:hover {
        background: transparent;
        border-color: #2c2c6c;
        cursor: default;
        color: black;
    }
    
    .about-icon {
        color: var(--dark-orange);
        font-size: 1.5rem;
        margin-bottom: 0.5rem;
    }
    
    .about-card h5 {
        font-size: 0.95rem;
    }
    
    .about-card small {
        font-size: 0.75rem;
        color: var(--disabled-btn);
        font-family: 'poppins light';
    }
    
    .about-content p {
        margin: 2% 5% 30px 5%;
        color: dimgrey;
        font-size: 16px;
        letter-spacing: 2px;
        word-spacing: 2px;
    }
    
    .lets {
        border-radius: 1rem;
        padding: 10px 40px;
        letter-spacing: 2px;
        word-spacing: 2px;
        text-decoration: none;
    }
    
    @media screen and (min-width:1110px) {
        .about-cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            margin: 0 15% 0 15%;
        }
    }
    
    @media screen and (max-width:1110px) {
        .about-cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            margin: 0 15% 0 15%;
        }
    }
    
    @media screen and (max-width:910px) {
        .about-cards {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            margin: 0 15% 0 15%;
        }
    }
    
    @media screen and (max-width:810px) {
        .about-cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            margin: 0 10% 0 10%;
        }
    }
    
    @media screen and (max-width:375px) {
        .about-cards {
            display: grid;
            grid-template-columns: 1fr;
            margin: 0 15% 0 15%;
        }
        .about-content {
            text-align: center;
        }
    }