#ContactMe h6,
#ContactMe h2 {
    text-align: center;
}

.contact-container {
    width: 60%;
    display: grid;
    justify-items: center;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin: 0 15% 0 15%;
}

.contact-options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.contact-option {
    /* background: #2c2c6c; */
    background: #24263b;
    padding: .5rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    color: white;
    transition: 1s ease-out;
}

.contact-option:hover {
    border-color: #24263b;
    background: transparent;
    color: black;
}

.option-icon {
    font-size: 1.5rem;
}

.link {
    background: transparent;
    color: var(--dark-orange);
    font-weight: 700;
    display: inline-block;
}

form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    align-items: center;
    width: 100%;
}

input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 1.5rem;
    background: transparent;
    border: 2px solid #24263b;
    resize: none;
}

@media screen and (min-width:1110px) {
    .contact-container {
        grid-template-columns: 30% 58%;
        gap: 12%;
    }
}

@media screen and (max-width:1110px) {
    .contact-container {
        grid-template-columns: 30% 58%;
        gap: 12%;
    }
}

@media screen and (max-width:910px) {
    .contact-container {
        grid-template-columns: 30% 58%;
        gap: 2rem;
    }
    input,
    textarea {
        width: 80%;
    }
}

@media screen and (max-width:810px) {
    .contact-container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

@media screen and (max-width:375px) {
    .contact-container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    input,
    textarea {
        width: 58%;
    }
}